.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  width: 100%;
  height: 80px;
  padding: 0 24px;
  box-sizing: border-box;
  box-shadow: 0 0 20px rgba(0, 0, 0, .07);
  background: #fff;
}

.logo-wrapper {
  position: relative;
  float: left;
  width: 120px;
  font-size: 24px;
  font-weight: bold;
  line-height: 80px;
  padding-left: 80px;
  height: 80px;
  background: url(/static/media/nlogo.5c1a10e1.svg) left center / auto 80px no-repeat;
}

.logo-wrapper .site-intro {
  position: absolute;
  right: -10px;
  top: 8px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url(data:image/png;base64,) center center / 100% auto no-repeat;
}

.nav-list {
  float: left;
  margin-left: 40px;
  padding-left: 20px;
  border-left: 1px solid #f5f5f5;
}

.nav-item {
  position: relative;
  display: inline-block;
}

.nav-item.selected:after {
  position: absolute;
  z-index: 1;
  content: '';
  bottom: 0;
  left: 0;
  right: 12px;
  height: 4px;
  background: RGBA(43, 190, 127, 1.00);
}

.nav-item a {
  display: inline-block;
  line-height: 24px;
  font-size: 16px;
  font-weight: bold;
  padding: 28px 24px 28px 12px;
  text-decoration: none;
  color: #2c2c2c;
}

.nav-item a:hover {
  color: RGBA(49, 63, 165, 1.00);
}

.profile-wrapper {
  position: relative;
  box-sizing: border-box;
}

.claimable-wing {
  float: right;
  margin: 18px 12px 0 0;
  padding: 0 20px;
  line-height: 44px;
  border-radius: 24px;
  background: RGBA(43, 190, 127, 0.1);
}

.account-address {
  position: relative;
  float: right;
  margin-top: 18px;
  padding: 0 40px;
  line-height: 44px;
  border-radius: 24px;
  cursor: pointer;
  background: RGBA(43, 190, 127, 0.1);
}

.account-address:before {
  position: absolute;
  content: '';
  left: 20px;
  top: 50%;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  border-radius: 4px;
  background: RGBA(43, 190, 127, 1.00);
}

.account-address:after {
  position: absolute;
  content: '';
  right: 20px;
  top: 0;
  bottom: 0;
  width: 10px;
  background: url(data:image/png;base64,) center / 10px auto no-repeat;
}

.account-address.triangle-up:after {
  transform: rotate(180deg);
  transition: all .5s;
}

.connect-btn {
  float: right;
  cursor: pointer;
  padding: 4px 10px;
  margin-top: 24px;
  border: 1px solid RGBA(43, 190, 127, 1.00);
  border-radius: 20px;
  color: RGBA(43, 190, 127, 1.00);
}

.profile-panel {
  position: absolute;
  top: 88px;
  right: 0;
  width: 280px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .07);
  background: #fff;
}

.setting-section-title {
  padding-left: 28px;
  font-weight: bold;
  background: url(data:image/png;base64,) left top / 20px auto no-repeat;
}

.slippage-setting {
  display: flex;
  margin-top: 12px;
}

.slippage-item {
  width: 60px;
  margin-right: 12px;
  line-height: 24px;
  font-size: 12px;
  text-align: center;
  border-radius: 20px;
  cursor: pointer;
  background: #E7E7E7;
}

.slippage-item.selected {
  color: #fff;
  background: RGBA(43, 190, 127, 1.00);
}

.slippage-input-wrapper {
  display: flex;
  flex: 1 1;
  border-bottom: 1px solid #949494;
}

.slippage-input-wrapper .slippage-input {
  width: 90px;
  padding: 0 8px 0 0;
  text-align: right;
  border: none;
  outline: none;
}

.signout-btn {
  margin: 24px auto 0;
  width: 120px;
  text-align: center;
  cursor: pointer;
  padding: 4px 10px;
  border-radius: 20px;
  color: #fff;
  background: RGBA(254, 51, 47, 1.00);

}

.modal-overlay {
  position: fixed;
  z-index: 21;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: black;
}

.modal-wrapper {
  position: absolute;
  min-width: 360px;
  left: 50%;
  top: 50%;
  padding: 20px 40px 40px;
  box-sizing: border-box;
  border-radius: 8px;
  transform: translate(-50%, -60%);
  background: #fff;
}

.modal-wrapper.transparent-bg {
  background: transparent;
}

.close-btn {
  position: absolute;
  right: 12px;
  top: 12px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: url(data:image/png;base64,) center center / 100% auto no-repeat;
}

.modal-icon {
  height: 100px;
  background: url(data:image/png;base64,) center center / auto 64px no-repeat;
}

.modal-icon-error {
  background-image: url(data:image/png;base64,);
}

.modal-icon-loading {
  height: 128px;
  background-image: url(/static/media/nlogo.5c1a10e1.svg);
  animation: loading 2s linear infinite;
}

.modal-icon-upgrading {
  width: 128px;
  height: 128px;
  margin: 0 auto;
  border-radius: 100px;
  background: #fff url(/static/media/upgrade.3603b963.svg) center / auto 64px no-repeat;
}

@keyframes loading {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.modal-text {
  font-size: 24px;
  font-weight: bold;
  padding: 16px 0;
  text-align: center;
  color: #2c2c2c;
}

.modal-text.light {
  color: white;
}

.modal-text-success {
  color: RGBA(43, 190, 127, 1.00);
}

.modal-text-info {
  color: rgb(23, 124, 255);
}

.modal-text-error {
  color: RGBA(254, 51, 47, 1.00);
}

.modal-detail {
  color: RGBA(254, 51, 47, 1.00);
  text-align: center;
  margin-bottom: 40px;
}

.modal-extra-text {
  text-align: center;
  font-size: 16px;
  color: #2c2c2c;
  word-break: break-all;
}

.transaction-link {
  color: #2c2c2c;
}
.trade-container {
  position: relative;
  width: 1000px;
  height: 640px;
  margin: 20px auto 0;
}

/* left panel */

.make-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 400px;
